export default {
	data() {
		return {
			// 登录或者是注册的类型
			type:'login',
			yzphone:false,
			phone:'',
			password:''
		}
	},
	methods: {
		// 手机号的格式进行检测
		inputPhone(e){
			if(/^1(3|4|5|6|7|8|9)\d{9}$/.test(e.detail.value)){
				this.yzphone=true;
			}else{
				this.yzphone=false;
			}
		},
		//忘记密码
		forPwd(){
			uni.navigateTo({
				  url: '../forgetPassword/forgetPassword'
			})
		},
		//手机验证码登录
		phoneVerifi(){
			uni.navigateTo({
				  url: '../phoneVerification/phoneVerification'
			})
		},
		//注册成功之后 重新设置登录密码
		jumps(){
			uni.navigateTo({
				url:'../setLoginPwd/setLoginPwd'
			})
		},
		loginclearPhone(){
			this.phone='';
		},
		switchTab(type){
			   console.log(type)
				uni.hideKeyboard();//隐藏软键盘
				this.type=type;
				this.phone='';
				this.password='';	
		}
	}
}
